.graph-branch-node,.graph-leaf-node,.graph-root-node {
  cursor: default!important
}

.graph-box {
  width: 100%;
  height: 100%
}

.graph-header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: -18px 0 10px;
  height: 18px
}

.graph-count {
  display: inline-block;
  background-color: #8083e6;
  text-align: center;
  border-radius: 15px;
  line-height: 30px;
  min-width: 30px;
  font-size: 16px;
  padding: 0 5px;
  color: #fff
}

.graph-image {
  border: 5px solid #8083e6;
  border-radius: 50%;
  margin: 5px auto 0;
  overflow: hidden;
  width: 60px;
  height: 60px
}

.graph-image img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%
}

.graph-image--interactive {
  transition: border-color .3s ease-in-out;
  cursor: pointer
}

.graph-image--interactive:hover {
  border-color: #cf64f3
}

.graph-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #4b0fb2;
  border-radius: 3px;
  cursor: pointer;
  height: 24px;
  padding: 3px
}

.graph-container button {
  background-color: transparent;
  color: #fff;
  border: none;
  flex: 1 1
}

.graph-container .icons {
  margin-top: 0
}

.graph-title {
  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  overflow: hidden;
  font-size: 12px;
  flex: 1 1
}

.search {
  margin-bottom: 40px
}

.search input[type=text] {
  display: flex;
  position: relative;
  align-items: center;
  background-color: #230b51;
  border: 1px solid #fff;
  border-radius: 35px;
  padding: 5px 15px;
  font-size: 14px;
  color: #fff;
  outline: none;
  height: 40px;
  width: 100%
}

.search input[type=text]::-webkit-input-placeholder {
  opacity: 1
}

.search input[type=text]:-ms-input-placeholder {
  opacity: 1
}

.search input[type=text]::placeholder {
  opacity: 1
}

@media (min-width: 1200px) {
  .search input[type=text] {
    padding:10px 20px;
    font-size: 20px;
    height: 60px
  }
}

.modal-content {
  background-color: #2a0072;
  border-radius: 10px
}

.modal-header {
  justify-content: center;
  border-bottom: none;
  padding: 20px
}

.modal-header .close {
  display: block;
  background: url(./media/close.ac2aaa1a.svg) no-repeat 50% 50%;
  background-size: contain;
  border: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px
}

.modal-header .close>img,.modal-header .close>span {
  display: none
}

.modal-header+.modal-body {
  padding-top: 0
}

@media (min-width: 1200px) {
  .modal-header {
    padding:40px
  }
}

.modal-title {
  margin-bottom: 0;
  line-height: 1.2;
  text-align: center;
  font-weight: 700;
  font-size: 24px
}

.modal-body {
  padding: 20px
}

.modal-body+.modal-footer {
  padding-top: 0
}

@media (min-width: 1200px) {
  .modal-body {
    padding:40px
  }
}

.modal-footer {
  border-top: none;
  padding: 20px
}

@media (min-width: 1200px) {
  .modal-footer {
    padding:40px
  }
}

.spinner-inner {
  padding: 100px 30px;
  text-align: center
}

.spinner-sm {
  padding-top: 25px;
  padding-bottom: 25px
}

.star-rating {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  margin: 7px 0;
  padding: 0
}

.star-rating--rtl {
  flex-direction: row-reverse
}

.star-rating--two-triangle li {
  position: relative
}

.star-rating--two-triangle li:first-child {
  top: -20px;
  order: 2
}

.star-rating--two-triangle li:nth-child(2) {
  order: 1
}

.star-rating--two-triangle li:nth-child(3) {
  order: 3
}

.star-rating--two-triangle li:nth-child(4) {
  top: -20px;
  right: -15px;
  order: 5
}

.star-rating--two-triangle li:nth-child(5) {
  left: 15px;
  order: 4
}

.star-rating--two-triangle li:nth-child(6) {
  left: 15px;
  order: 6
}

.star-rating li+li {
  margin-left: 6px
}

.star-rating li.interactive {
  cursor: pointer
}

.card {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: rgba(42, 0, 114, 0);
  word-wrap: break-word;
  border-radius: 25px;
  margin-bottom: 25px;
  min-width: 0
}

.card__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px
}

.card__header+.card__body {
  padding-top: 0
}

.card__title {
  margin-bottom: 0;
  line-height: 24px;
  font-weight: 700;
  font-size: 20px
}

.card__body {
  padding: 25px
}

.card__body+.card__footer {
  padding-top: 0
}

.card__footer {
  padding: 25px
}

.card__actions--center {
  text-align: center
}

.button-style {
  left: calc(50% - .5rem)
}

.span-style {
  margin-right: 1rem
}

.icons {
  display: inline-block;
  vertical-align: middle;
  stroke: currentColor;
  margin-top: -.2em;
  fill: currentColor;
  width: 1em;
  height: 1em;
  stroke-width: 0
}

.referral-link {
  display: flex;
  align-items: center
}

.referral-link__url {
  font-weight: 500;
  cursor: default;
  flex: 1 1
}

.referral-link__btn {
  color: #fff
}

.form-control-label {
  margin-bottom: 0;
  line-height: 18px;
  font-size: 14px
}

select.form-control,select.form-control:focus {
  border: none;
  border-bottom: 1px solid #fff;
  background-color: transparent;
  box-shadow: none;
  border-radius: 0;
  padding-left: 0;
  color: #fff
}

.list-info--horizontal .list-info__group {
  display: flex;
  align-items: center;
  justify-content: space-between
}

@media (min-width: 768px) {
